// Custom Theming for Angular Material
// For more information: https://material.angular.io/guide/theming
@import "~@angular/material/theming";
// Plus imports for other components in your app.

// Include the common styles for Angular Material. We include this here so that you only
// have to load a single css file for Angular Material in your app.
// Be sure that you only ever include this mixin once!
@include mat-core();

// Define the palettes for your theme using the Material Design palettes available in palette.scss
// (imported above). For each palette, you can optionally specify a default, lighter, and darker
// hue. Available color palettes: https://material.io/design/color/
$sean-meek-app-primary: mat-palette($mat-indigo);
$sean-meek-app-accent: mat-palette($mat-pink, A200, A100, A400);

// The warn palette is optional (defaults to red).
$sean-meek-app-warn: mat-palette($mat-red);

// Create the theme object (a Sass map containing all of the palettes).
$sean-meek-app-theme: mat-light-theme(
	$sean-meek-app-primary,
	$sean-meek-app-accent,
	$sean-meek-app-warn
);

// Include theme styles for core and each component used in your app.
// Alternatively, you can import and @include the theme mixins for each component
// that you are using.
@include angular-material-theme($sean-meek-app-theme);

/* You can add global styles to this file, and also import other style files */
@import "src/styles/variables.scss";

@font-face {
	font-family: lato-light;
	src: url("/assets/font/Lato-Light.ttf") format("truetype");
}

@font-face {
	font-family: fira-reg;
	src: url("/assets/font/FiraMono-Regular.ttf") format("truetype");
}

html,
body {
	height: 100%;
	background-color: $background-color;
	font-size: 18px;
	color: $text-color;
	font-weight: 500;
	margin: 0;
	font-family: $font-family-sans-serif;
	line-height: 26px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-rendering: optimizeLegibility;
	text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}

h3 {
	font-weight: 350;
}

h4 {
	margin-top: 30px;
	margin-bottom: 20px;
}

p {
	font-size: 1rem;
	font-weight: 400;
	line-height: 26px;
}

a.sm-text-link {
	display: inline-block;
	text-decoration: none;
	transition: color 0.2s ease-out;

	&:hover {
		cursor: pointer;

		&.sm-change-text-color {
			color: $highlight;
			transition: color 0.5s ease-out;
		}
	}

	&::after {
		content: "";
		display: block;
		margin-top: -4px;
		width: 0;
		height: 1px;
		background: $highlight;
		transition: width 0.2s ease-out;
	}

	&:hover::after {
		width: 100%;
		transition: width 0.3s ease-out;
	}
}

.content-container {
	margin-top: 24px;
	margin-bottom: 24px;
	margin-left: 16px;
	margin-right: 16px;
}

.center-contents {
	display: flex;
	justify-content: center;
}

// SVG definitions are loaded dynamically as the definitions are stored on the page outside of the
//	component it's loaded into. Therefore, this needs to be here instead of the component.
svg {
	width: 100%;
	height: 100%;
}

sm-code-block {
	code {
		font-family: $font-family-monospace;
	}
}

.mdl-card {
	border-radius: $border-radius;
}

.mdl-button--colored {
	color: $highlight !important;
}
